html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Varela Round', 'Ariel', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: grid;
  background-color: rgba(2, 0, 36, 1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App {
  color: #fff;
  text-align: center;
  width: inherit;
  height: inherit;
}

h1 {
  margin: 10px 0px 16px;
  font-size: 72px;
  line-height: 54px;
  font-weight: 900;
  letter-spacing: 1px;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, 1), 4px 2px 4px rgba(0, 0, 0, .85);
}

h2 {
  margin: 10px 0px;
  font-size: 30px;
  line-height: 36px;
  font-weight: 700;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, .84), 4px 2px 4px rgba(0, 0, 0, .85);
}

h3 {
  margin: 0;
  font-size: 22px;
  line-height: 23px;
  font-weight: bold;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, .84), 4px 2px 4px rgba(0, 0, 0, .85);
}

h4 {
  margin: 10px 0px;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, .84), 4px 2px 4px rgba(0, 0, 0, .85);
}

h5 {
  margin: 10px 0px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, .84), 4px 2px 4px rgba(0, 0, 0, .85);
}

h6 {
  margin: 10px 0px;
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, .84), 4px 2px 4px rgba(0, 0, 0, .85);
}

p {
  margin-bottom: 5px;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 1px;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, .84), 4px 2px 4px rgba(0, 0, 0, .85);
}

u {
  text-decoration: none;
  border-bottom: 10px solid #D7AA27;
  border-radius: 15px;
}

.Raccoon,
.question {
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  /* Animation */
}

.Raccoon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

.question:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

@media screen and (max-width: 900px) {
  .App {}
}
.Nav {
  width: 100%;
  position: fixed;
  z-index: 10000;
  background-color: #020024;
}

.Nav ul {
  float: right;
  padding: 0 25px;
}

.Nav li {
  display: inline-block;
  vertical-align: middle;
  margin: 0 25px;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
  /* Animation */
}

.Nav .link {
  font-size: 18px;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}

.Nav .link:hover {
  color: #D7AA27;
}

.Nav li:hover {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

/* Position and sizing of burger button */
.bm-burger-button {
  display: none;
  position: fixed;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #fff;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #fff;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 36px;
  width: 36px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #fff;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.5em;
  margin-bottom: 5px;
  line-height: 30px;
  letter-spacing: 1px;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

@media screen and (max-width: 800px) {
  .Nav .wide-navbar {
    display: none;
  }

  .bm-burger-button {
    display: block;
  }
}
.Home {
  width: 100%;

  position: relative;
  background: rgba(2, 0, 36, 1);
  background: linear-gradient(360deg, #6A83A8 10%, #daad27 10%, #09111D 35%, #020024 80%);
}

.Home .container {
  height: 100vh;
  width: 100%;
  display: table;
  position: relative;
}

.Home .bg {
  background-image: url(/static/media/UCM.97a4b604.png);
  background-position: bottom 10% center;
  background-repeat: no-repeat;
  background-attachment: absolute;
  background-size: 3200px;
}

.content {
  height: inherit;
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  text-align: center;
}

.button {
  display: inline-block;
  background-color: #D7AA27;
  border: none;
  border-radius: 30px;
  font-size: 28px;
  text-shadow: 0 3px 3px rgba(0, 0, 0, .84), 0 2px 4px rgba(0, 0, 0, .45);
  box-shadow: 0 3px 3px rgba(0, 0, 0, .84), 0 2px 4px rgba(0, 0, 0, .45);
  width: 160px;
  padding: 10px 10px;
  margin: 40px 10px 0;
  color: #fff;
  text-decoration: none;
}

a.button:hover {
  background-color: #F4D03F;
}

@media screen and (max-width: 700px) {
  .Home h1 {
    font-size: 48px;
  }
}

@media screen and (max-width: 650px) {
  .Home {}

  .Home .container {
    background-size: 1600px;
  }
}

@media screen and (max-height: 800px) {
  .Home .container {
    background-size: 2000px;
  }
}
.About {
  width: inherit;
  padding-top: 100px;
  position: relative;
  background-color: #324157;
}

.About .container {
  margin: 0 auto;
  padding: 0 20px;
}

.About .text {
  display: inline-block;
  width: 600px;
  padding: 0 30px;
  text-align: left;
  vertical-align: middle;
}

.info {
  margin: 40px 0 20px;
}

.goal {
  margin: 20px 0 0 0;
}

.About .image {
  display: inline-block;
  vertical-align: middle;
}

@media screen and (max-width: 1200px) {
  .About {
    padding-top: 100px;
  }

  .About .container {
    width: inherit;
  }
}

@media screen and (max-width: 700px) {
  .About .text {
    width: 400px;
  }
}

@media screen and (max-width: 500px) {
  .About .text {
    width: 300px;
  }
}
.Faqs {
  width: inherit;
  padding: 100px 0;
  position: relative;
  background-color: #020024;
}

.Faqs .container {
  display: -webkit-flex;
  display: flex;
  width: 1150px;
  margin: 0 auto;
}

.Faqs .column {
  -webkit-flex: 50% 1;
          flex: 50% 1;
}

.left {
  margin-right: 20px;
}

.right {
  margin-left: 20px;
}

.Faqs .question {
  text-align: left;
  border: 4px solid #D7AA27;
  border-radius: 15px;
  padding: 35px;
  margin: 40px 0;
}

.Faqs.prompt {
  padding: 0 20px;
}

@media screen and (max-width: 1200px) {

  .Faqs .container {
    display: block;
    width: inherit;
  }

  .left,
  .right {
    margin: 0 20px;
  }
}
.Sponsors {
  width: inherit;
  padding-top: 100px;
  position: relative;
  background-color: #09111D;
}

.Sponsors .container {
  width: 1100px;
  padding: 0 20px;
  margin: 0 auto;
}

.Sponsors .rows {
  -webkit-flex: 50% 1;
          flex: 50% 1;
}

.icon {
  margin: 10px 20px;
  display: inline-block;
  vertical-align: middle;
}

.rows .icon, .supporters .icon {
  width: 250px;
}

.top {
  margin-top: 30px;
}

.Citris img {
  width: 350px;
}

.Ucanr img {
  width: 600px;
  margin: 20px 0;
}

.Venture img {
  width: 550px;
}

.rows img {
  width: 200px;
}

.StickerMule img {
  width: 250px;
}

.CoolerMaster img {
  width: 160px;
}

.RedBull img {
  margin-top: 30px;
  width: 180px;
}

.Merco img {
  width: 100px;
}

.supporters img {
  width: 200px;
}

.Vanguard img, .ASUCM img {
  width: 150px;
}

.Sponsors.prompt {
  padding: 0 20px;
}

.Sponsors .Raccoon {
  margin-top: 40px;
  width: 200px;
}

.sponsoring, .supporters {
  margin-bottom: 50px;
}

.icon img {
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s; /* Animation */
}
.icon img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

@media screen and (max-width: 1100px) {
  .Sponsors .container {
    width: 650px;
  }
}

@media screen and (max-width: 700px) {
  .Sponsors .container {
    width: 440px;
  }

  .icon {
    margin: 20px auto;
  }

  .Ucanr img {
    width: 400px;
  }

  .Venture img {
    width: 400px;
  }
}

@media screen and (max-width: 500px) {
  .Sponsors .container {
    width: 320px;
  }

  .Citris img {
    width: 300px;
  }
  .Ucanr img {
    width: 300px;
  }

  .Venture img {
    width: 300px;
  }
}
.Footer {
  width: inherit;
  padding-top: 50px;
  position: relative;
  background-color: #09111D;
}

.copyright {
  font-size: 16px;
}

.fas,
.fab {
  color: #fff;
  font-size: 32px;
  margin: 20px;
}
