.Nav {
  width: 100%;
  position: fixed;
  z-index: 10000;
  background-color: #020024;
}

.Nav ul {
  float: right;
  padding: 0 25px;
}

.Nav li {
  display: inline-block;
  vertical-align: middle;
  margin: 0 25px;
  transition: transform .2s;
  /* Animation */
}

.Nav .link {
  font-size: 18px;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}

.Nav .link:hover {
  color: #D7AA27;
}

.Nav li:hover {
  transform: scale(1.4);
  /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

/* Position and sizing of burger button */
.bm-burger-button {
  display: none;
  position: fixed;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #fff;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #fff;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 36px;
  width: 36px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #fff;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.5em;
  margin-bottom: 5px;
  line-height: 30px;
  letter-spacing: 1px;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

@media screen and (max-width: 800px) {
  .Nav .wide-navbar {
    display: none;
  }

  .bm-burger-button {
    display: block;
  }
}